* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-top: 10px;
  margin-bottom: 16px;
  margin-left: 10px;
  padding: 8px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.list__item {
  max-width: 220px;
  align-items: center;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: 600;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.list__item:hover {
  transform: scale(1.05);
}

.list__image {
  flex-basis: 15%;
  margin-right: 15px;
}

.country-info {
  max-width: 200px;
  padding: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.country-info:hover {
  transform: scale(1.05);
}

.country-wrapper {
  align-items: center;
  margin-bottom: 20px;
  padding: 10px 0;
  display: flex;
}

.country-flag-wrapper {
  flex-basis: 33%;
}

.name {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 700;
}

.capital, .population, .languages {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
}

.text {
  margin-left: 10px;
  font-weight: 500;
}

